body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  text-align: center;
  font-family: 'Gruppo', cursive;
}

footer{
  font-size: 0.8em;
  text-shadow: 2px 3px 4px #a8e6ff;
}
ul{
    list-style: none;
}

li{
    float:left; 
    padding-left: 20px;
    padding-right: 30px;
    padding-bottom: 50px;
}

.inputField{
    background-color: #282c34;
    border: 2px solid #ffffff;
    border-radius: 30px;
    padding: 12px 20px;
    color: white;
}

.inputField::-webkit-input-placeholder{
    color: rgb(179, 179, 179);
}

.inputField::-ms-input-placeholder{
    color: rgb(179, 179, 179);
}

.inputField::placeholder{
    color: rgb(179, 179, 179);
}

/* Hide the Placeholder text when focused */
input:focus::-webkit-input-placeholder { 
    color: transparent; 
}

.inputField:hover{
    background-color: #282c34;
    border: 2px solid #42c3ff;
    box-shadow: 0 0 12px #8cd1ff;
    border-radius: 30px;
    padding: 12px 20px;
    color: rgb(78, 255, 187);
}

.inputField:focus{
    border-color: rgb(106, 109, 255);
    box-shadow: 0 0 12px #8cd1ff;
    border-radius: 30px;
    padding: 12px 20px;
    outline: none;
}

.gradButtonWrapper{
    padding: 4px;
}

.gradButtonContainer{
    padding: 4px;
}

.gradButton{
    background-color: #282c34;
    border: 2px solid #ffffff;
    color: #ffffff;
    padding: 8px 14px 8px 14px;
    text-align: center;
    font-family: 'Gruppo', cursive;
    display: inline-block;
    font-size: 16px;
    border-radius: 20px;
}

.gradButton:hover {
    border-color: rgb(73, 228, 255);
    box-shadow: 0 0 12px #baff8c;
    color: rgb(104, 151, 253);
}

.submitButton{
    display: inline-block;
    background-color: #282c34;
    border: 2px solid #ffffff;
    padding: 20px 16px 20px 16px;
    color: #ffffff;
    text-align: center;
    font-family: 'Gruppo', cursive;
    font-size: 24px;
    border-radius: 40px;
}

.submitButton:hover {
    border-color: rgb(150, 255, 173);
    box-shadow: 0 0 12px #b8d7ff;
    color: rgb(251, 251, 255);
}
.outputTable td{
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 4px;
    padding-bottom: 4px;

    border-top: 1px solid white;
    border-bottom: 1px solid white;
}

.outputTable{
    border-collapse: collapse;
    text-align: center;
}
